#contactSection {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	height: 100vh;
	background-color: var(--contactBackground);
}
#contactSection  > div {
	-ms-flex: 1;
	    flex: 1 1;
}
#contactContent {
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.contactparttop{
	height: 70%;
	width: 50%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center
}
.iconColor {
	height: auto;
	color: var(--iconsContact);
}
.contactIcons {
	font-size: 20vh;
}
.contactpartbottom{
	height: 30%;
	width: 100%;
	font-family: 'Lato', Arial, sans-serif;
	font-size: 3vw;
}
.email {
	width: 100%;
	height: 5%;
	text-align: center;
}
.emailLink {
	text-decoration: none;
	color: var(--iconsContact);
}
@media  screen  and (max-width:700px)  {
	.emailLink {
		font-size: 4vh;
	}
	.contactIcons {
		font-size: 15vh;
	}
}
@media (pointer:fine) {
	.iconColor :hover {
		color: var(--hoverIconsContact);
	}
	.contactpartbottom :hover{
		color: var(--hoverIconsContact);
	}
	.email:hover {
		-webkit-animation: contactBeat 1s infinite alternate;
    	animation: contactBeat 1s infinite alternate;
	}
	.contactIcons:hover {
		-webkit-animation: contactBeat 1s infinite alternate;
    	animation: contactBeat 1s infinite alternate;
	}
	@-webkit-keyframes contactBeat {
	  to {
	    -webkit-transform: scale(1.2);
	            transform: scale(1.2);
	  }
	}
	@keyframes contactBeat{
	  to {
	    -webkit-transform: scale(1.2);
	            transform: scale(1.2);
	  }
	}
}


