#projectSection {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	height: 100vh;
}
#projectSection > div {
	-ms-flex: 1;
	    flex: 1 1;
}
#projectContent {
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	background-color: var(--projectsBackground);
}
#projectContent > div {
	margin: auto;
}
.carousel, .carousel-slider, .slider-wrapper, .axis-horizontal, .slider, .slide {
	height: 100%;
	background-color: var(--projectsBackground);
}
#projectContent > div {
	width: 100%;
	cursor: -webkit-grabbing;
	cursor: grabbing;
}
#projectContent > div > div:nth-child(2) {
	display: none;
}
.thumbs-wrapper {
	display: none;
}
.project {
	background-color: var(--projectsBackground);
	height:100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
  	-ms-flex-pack: center;
  	    justify-content: center;
  	font-family: 'Lato', Arial, sans-serif;
}
.control-dots {
	display: none;
}
.projectTitleContainer {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}
.projectTitle {
	font-family: 'Righteous', cursive;
	margin: 0;
	font-size: 4vw;
	text-transform: uppercase;
	color: var(--projectTitle);
	padding-bottom: 10px ;
}
.projectImageContainer {
	width: 30%;
}
.projectImage {
	width: auto;
	height: 100%;
	-o-object-fit: contain;
	   object-fit: contain;
}
.projectInfoContainer {
	width: 70%;
}
.projectDescriptionContainer {
	height: 33%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center
}
.projectDescription {
	margin: 0;
	font-size: calc(5px + 1.7vw);
	width: 80%;
}
.projectTechContainer {
	height: 33%;
	padding: 3px;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-line-pack: start;
	    align-content: flex-start;
	list-style-type: none;
	-ms-flex-pack: center;
	    justify-content: center;
}
.techPiece {
	height: 4vw;
	font-size: 2vw;
	padding: 4px;
	margin: 5px;
	border: 2px solid black;
	border-radius: 12px;
	background-color: var(--techPiece);
	-webkit-box-shadow: 2px 2px grey;
	        box-shadow: 2px 2px grey;
}
.buttonHolder {
	height: 33%;
	width: 100%;
	text-align: center;
	-ms-flex-pack: center;
	    justify-content: center;

}
.demoButton {
	display: inline;
	font-size: 3vw;
	margin: auto;
	padding: 5px;
	padding-left:20px;
	padding-right:20px;
	text-transform: uppercase;
	width: 35vw;
	border-radius: 15px;
	-webkit-box-shadow: 2px 2px grey;
	        box-shadow: 2px 2px grey;
	color: var(--openAppText);
	background-color: var(--openApp);
	text-transform: uppercase;
	cursor: pointer;
}
.link {
	text-decoration: none;
}
.fluffer {
	height: 50%;
	width: 100%;
}
/*For Desktop*/
@media  screen  and (min-width: 650px) {
	.projectInfoContainer {
		width: 60%;
		font-size: 2vh;
		text-align: justify;
	}
	.projectDescription {
 		font-size: 2vw;
	}
	.projectDescriptionContainer {
		height: 50%;
	}
	.projectTechContainer {
		height: 25%;
	}
	.buttonHolder {
		height: 25%;
	}
}
@media (pointer: fine) {
	.demoButton:hover{
		background-color: var(--openAppHover);
	}
}
/*For mobile -Portrait Mode*/
@media  only screen  and (max-width: 500px)  and (orientation: portrait) {
	.projectTitle{
		font-size: 3vh;
	}
	.projectImageContainer {
		height: 45%;
		width: 100%;
		text-align: center;
	}
	.projectInfoContainer {
		width: 100%;
		height: 50%;
	}
	.projectDescription {
		font-size: 3vh;
		width: 95%;
	}
	.techPiece {
		height: 4vh;
		font-size: 2vh;
	}
	.demoButton {
		font-size: 3vh;
		margin: auto;
		padding: 5px;
		text-transform: uppercase;
		width: 35vh;
	}

	@supports (-webkit-overflow-scrolling: touch) {
	  /* CSS specific to iOS devices */
	  	.projectImageContainer {
			height: 30%;
			width: 70%;
		}
	}

}



