html {
    height: 100%;
}
body {
	overflow-x: hidden  !important;
	margin: 0;
    padding: 0;
    height: 100%;
}

/*Master CSS Colors*/
:root {
	/*Title Section*/
	  --titleJeremyLevett: #FF1800;
	  --subtitleJeremyLevett:black;
	  --downArrow:#006255;
	  --titlespacer:transparent;
	  --backgroundTitlePage:transparent;
	/*About*/
		--aboutBackground:#79BDB4;
	/*Software Skills*/
		--skillsBackground: #79BDB4;
		--skillText:#F96170;
	/*Projects*/
		--projectsBackground: #79BDB4;
		--projectTitle:#F73749;
		--techPiece:#A5A5A5;
		--openAppText:#FFB839;
		--openApp:#006255;
		--openAppHover:#007C6B;

	/*Other Skills*/
		--otherBackground: #79BDB4;
	/*Outside Work*/
		--interestsBackground: #79BDB4;
	/*Contact Me*/
		--contactBackground:transparent;
		--hoverIconsContact: #F73749;
		--iconsContact:black;
		--emailCopied: #F73749 ;
	/*Section General*/
	--sectionTitle:#F73749;
	--sectionBackground:#FFB839;
	--sectionTitleSpacers:#79BDB4;
}

#preloaderContainer {
	background-color: #FFB839;
	width:100%;
	height:100vh;
	display: -ms-flexbox;
	display: flex;
  	-ms-flex-align: center;
  	    align-items: center;
   	-ms-flex-pack: center;
   	    justify-content: center;
}
#preloaderItems {
	 width:100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.preloaderItemsContainer{
	width:100%;
	display: -ms-flexbox;
	display: flex;
  	-ms-flex-align: center;
  	    align-items: center;
   	-ms-flex-pack: center;
   	    justify-content: center;
}

#loadingText {
	font-size: 4vh;
	color:#006255;
	font-family: 'Righteous', cursive;
}



